export var removeAllInjectedSagas = function (store) {
    // get injected sagas
    var injectedSagas = store.asyncSagas;
    // delete sagas from injected sagas registry and stop them
    Object.keys(injectedSagas).forEach(function (sagaName) {
        injectedSagas[sagaName].cancel();
        delete injectedSagas[sagaName];
    });
};
